import React from 'react';
import {Link} from 'react-router-dom';
function Menu() {
  return (
    <>
        <div className='main-menu'>

            <ul>
                <li><Link to='/home'>Home</Link> </li>
                <li><Link to='/upload'>Upload</Link></li>
                <li><Link to='/delete'>Delete</Link> </li>
            </ul>
        </div>
    </>
  )
}

export default Menu