import React from 'react'
import { useState } from 'react';
import Base from './comComponent/Base';
import { addImage } from './helper/fetchData';

function Upload() {
    const [data, setData] = useState({
        name:'',
        phone:'',
        email:'',
        image:''

    });

    const onSubmit = (event)=>{
        event.preventDefault();
        console.log('Yeah you have submitted your form', data);
        addImage(data).then(res=>{
            if(res){
                console.log(res);
                setData({
                    name:'',
                    phone:'',
                    email:'',
                    image:''
            
                })
            }
        })
    }
    const updateFromData = field => (e)=>{
        const value = field === "image" ? e.target.files[0] : e.target.value;
        setData({...data, [field]: value });
    }
    const categoryForm = () => {
        return(
            <div className='upload-Form-container'>
                <form action="">
                    <div className="form-group">
                        <h2 className="">Upload your images</h2>
                        <input type="text" value={data.name}  placeholder='name' onChange={updateFromData('name')} />
                        <input type="text" value={data.email} placeholder='email' onChange={updateFromData('email')} />
                        <input type="text" value={data.phone} placeholder='phone' onChange={updateFromData('phone')} />
                        <input type="file" name="image" accept="image" onChange={updateFromData('image')} />
                        <button onClick={onSubmit} className="uploadBtn" >Submit</button>
                    </div>
                </form>

            </div>
        )
    }
  return (
    <Base>
        {categoryForm()}
    </Base>
  )
}

export default Upload;