import React from 'react'
import { useState } from 'react';
import Base from './comComponent/Base';
import { deleteImg } from './helper/fetchData';

function Deleteimg() {
    const [imgId, setImgId] = useState('');

    const onSubmit = (event)=>{
        event.preventDefault();
        console.log(imgId);
        deleteImg(imgId)
        .then(res=>{
            console.log('final fornted respionse',res)
        })
        .catch(error => {
            console.log('fincal err', error)
        })
    }
  return (
    <Base>
        <div className='deleteForm'>
            <form action="">
                <input type="text" value={imgId} placeholder='Enter image Id' onChange={(e)=>setImgId(e.target.value)}/>
                <button onClick={onSubmit}>Delete</button>
            </form>

        </div>
    </Base>
  )
}

export default Deleteimg