exports.fetchAllimages = ()=>{
    return fetch('/user/getallusers',{
        method: 'GET',
        headers:{
            "Accept":'*/*',
            "Content-Type":"application/json"
        }

    }).then(el=>{
        return el.json();
    }).then(res=>{
        return res;
    })
}

exports.addImage = (userData)=>{
    const formData = new FormData();
    formData.append("name", userData.name);
    formData.append("email", userData.email);
    formData.append("phone", userData.phone);
    formData.append("image", userData.image);

    return fetch('/user/create',{
        method: 'POST',
        redirect: 'follow',
        body: formData
    })
    .then(res =>{
        // console.log('FILE UPDLOADE RESPONSE',res);
        return res.text();
    })
    .then( response =>{
        return response;
    })
    .catch( error => {
        console.log('Error: ', `${error}`);
    })
}


exports.deleteImg = (imgId)=>{
    fetch(`/user/delete/${imgId}`,{
        method: 'DELETE'
    })
    .then(res=>{
        console.log(res);
        return res.json();
    })
    .then(result =>{
        console.log(result);
    })
    .catch(error => {
        console.log(error);
    })

}