import React, { useEffect } from 'react'
import { useState } from 'react';
import Base from './comComponent/Base';
import { fetchAllimages } from './helper/fetchData'

function HomePage() {
    const [data, setData] = useState([]);
    useEffect(()=>{
        async function toawait(){
            const {users} = await fetchAllimages();
            setData(users||[]);
        }
        toawait();
        // setData()
    },[])
  return (
    <>
        <Base>
            <div>
                <h1 style={{textAlign:'center'}}>See this iamges</h1>
                <div style={{display: 'flex', flexWrap:'wrap', justifyContent:'center'}}>
                    {data?.map((el,index)=>{
                    return <>
                            <div key={index} style={{display: 'flex', flexDirection:'column', width: 'fit-content', margin:'20px', alignItems:'center'}}>
                                <h3 style={{display: 'inlineBlock', textTransform:'uppercase'}}>{el.name}</h3>
                                <img style={{width:'300px', height:'230px', borderRadius:'10px'}} data-value={el._id} src={`/${el.profileImage}`} alt="soter" />
                            </div>
                        </>
                        
                    })}

                </div>
            </div>
        </Base>
    </>
  )
}

export default HomePage